.App {
  text-align: center;
}

* {
  margin:0;
  padding:0;
}

a {
  text-decoration: none;
  color: #010101;
}

body {
  background: #1d1d1d;
}

.card-container {
  width:1200px;
  margin:0 auto;
  /* background:red; */
  display: flex;
  flex-wrap:wrap;
  background:#1d1d1d;
}

.item-card {
  width: 30%;
  margin: 30px auto;
  /* background:blue; */
  color:#efefef;
  /* transform: scale(0); */
  opacity:0;
  animation: enter 1s linear forwards;
}

.trans-img {
  width: 70%;
  border-bottom-right-radius:50%;
  border-bottom-left-radius:50%;
}


.rare {
  background: radial-gradient(#78AECE, #1d1d1d 40%);
  color: #78AECE;
}
.uncommon {
  background: radial-gradient(#93CF86, #1d1d1d 40%);
  color: #93CF86;
}
.epic {
  background: radial-gradient(#C680EC, #1d1d1d 40%);
  color: #C680EC;
}
.legendary {
  background: radial-gradient(#FFCC7B, #1d1d1d 40%);
  color: #FFCC7B;
}


.cost-container {
  display:flex;
  width:100%;
  justify-content:center;
}
.cost {
  font-size: 150%;
  margin-left:-10px;
}
.currency {
  height:20px;
  width:20px;
  position: relative;
  top:7px;
  left:-10px;
}



@keyframes enter {
  100% {
    /* transform: scale(1); */
    opacity:1;
  }
}